import React, { useState, useEffect } from "react";
import moment from 'moment';
import { usersService } from "./_services";
import { DeleteOutlined } from "@ant-design/icons";
import "./App.css";
import "antd/dist/antd.css";
import {
  Form,
  Input,
  Button,
  Row,
  Col,
  DatePicker,
  message,
  Table,
} from "antd";

function App() {
  const [loadTable, setLoadTable] = useState(false);
  const [data, setData] = useState([]);

  const columns = [
    {
      dataIndex: "id",
      key: "id",
      width: 0,
      align: "center",
      render: (text, record) => {
        "";
      },
    },
    {
      title: "Username",
      dataIndex: "username",
      key: "username",
      width: 400,
      align: "center",
    },
    {
      title: "Fullname",
      dataIndex: "fullname",
      key: "fullname",
      width: 400,
      align: "center"
    },
    {
      title: "Birthdate",
      dataIndex: "birthdate",
      key: "birthdate",
      width: 400,
      align: "center",
      render: (text, record) => {
        return moment(text).format('MM/DD/YYYY');
      }
    },
    {
      title: "E-mail",
      dataIndex: "email",
      key: "email",
      width: 400,
      align: "center",
    },
    {
      title: "Image URL",
      dataIndex: "image",
      key: "image",
      width: 400,
      align: "center",
    },
    {
      title: "Delete",
      dataIndex: "delete",
      key: "delete",
      align: "center",
      width: 180,
      render: (text, record) => (
        <div onClick={() => deleteUser(record)}>
          <DeleteOutlined
            style={{ fontSize: "18px", color: "rgb(255, 77, 79)", cursor: "pointer" }}
          />
        </div>
      ),
    },
  ];

  useEffect(() => {
    getData();
  }, []);

  const getData = async () => {
    setLoadTable(true);
    let users = await usersService.getUsers();
    setData(users);
    setLoadTable(false);
  };

  const deleteUser = async (rec) => {
    let isDelete = window.confirm(`Delete user: \n${rec.username}?`);
    if (isDelete) {
      setLoadTable(true);
      await usersService.deleteUser( { id: rec.id, username: rec.username });
      message.success(`User ${rec.username} deleted`, 3);
      setLoadTable(false);
      getData();
    }
  };

  const onFinish = async (values) => {
    let createRecord = await usersService.createUser(values);
    if (
      typeof createRecord !== "undefined" &&
      createRecord !== "undefined" &&
      createRecord !== null
    ) {
      message.success(`User created: ${values.username}`, 3);
      getData();
    }
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const formItemLayout = {
    labelCol: {
      xs: {
        span: 24,
      },
      sm: {
        span: 8,
      },
    },
    wrapperCol: {
      xs: {
        span: 24,
      },
      sm: {
        span: 16,
      },
    },
  };

  return (
    <>
      <Row xl={24} style={{ paddingTop: "50px" }}>
        <Col xl={7}></Col>
        <Col xl={8}>
          <Form
            {...formItemLayout}
            name="basic"
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
          >
            <Form.Item
              label="Username"
              name="username"
              rules={[
                {
                  required: true,
                  message: "Please input your username!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Fullname"
              name="fullname"
              rules={[
                {
                  required: true,
                  message: "Please input your fullname!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Birthdate"
              name="birthdate"
              rules={[
                {
                  required: true,
                  message: "Please input your birthdate!",
                },
              ]}
            >
              <DatePicker style={{ width: "100%" }} />
            </Form.Item>

            <Form.Item
              name="email"
              label="E-mail"
              rules={[
                {
                  type: "email",
                  message: "The input is not valid E-mail!",
                },
                {
                  required: true,
                  message: "Please input your E-mail!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Password"
              name="password"
              rules={[
                {
                  required: true,
                  message: "Please input your password!",
                },
              ]}
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              label="Profile image URL"
              name="image"
              rules={[
                {
                  required: true,
                  validator: (_, value) =>
                  !value.match(/^(?:(?:https?|ftp):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/)
                  ? Promise.reject('Does not match URL!') : Promise.resolve()
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              wrapperCol={{
                offset: 8,
                span: 16,
              }}
            >
              <Button type="primary" htmlType="submit">
                Add user
              </Button>
            </Form.Item>
          </Form>
        </Col>
        <Col xl={8}></Col>
      </Row>

      <Row xl={24} style={{ paddingTop: "50px" }}>
        <Table
          loading={loadTable}
          tableLayout="auto"
          style={{ width: "100%", paddingBottom: "10px" }}
          columns={columns}
          dataSource={data}
        />
      </Row>
    </>
  );
}

export default App;
