import * as config from '../config.json';
const connectionConfig = config.default;

export const getConfig = () => {
  const {
    location: { hostname },
  } = window;
  const NODE_ENV = process.env.NODE_ENV;
  let URL = hostname;
  console.log(URL);
  return {
    ...connectionConfig,
  apiBaseURL: 'http://'+URL+'/api'
  };
};
