import axios from "axios";
import { getConfig } from "../_helpers";

const getUsers = () => {
  const connectionConfig = getConfig();
  const requestOptions = {
    method: "GET",
    headers: { "Content-Type": "application/json" },
    baseURL: connectionConfig.apiBaseURL,
    url: "/users",
    auth: {
      username: connectionConfig.apiLoginValue,
      password: connectionConfig.apiPasswordValue,
    },
  };

  return axios(requestOptions).then((user) => {
    const dep = user.data;
    return dep;
  }).catch((error) => {
    alert(error);
  });
};

const deleteUser = (params) => {
  const connectionConfig = getConfig();
  const requestOptions = {
    method: "DELETE",
    headers: { "Content-Type": "application/json" },
    baseURL: connectionConfig.apiBaseURL,
    url: `/deleteUser/${params.id}`,
    data: { ...params},
    auth: {
      username: connectionConfig.apiLoginValue,
      password: connectionConfig.apiPasswordValue,
    },
  };

  return axios(requestOptions).then((user) => {
    const dep = user.data;
    return dep;
  }).catch((error) => {
    alert(error);
  });
};

const createUser = (params) => {
  const connectionConfig = getConfig();
  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    baseURL: connectionConfig.apiBaseURL,
    url: "/createUser",
    data: { ...params },
    auth: {
      username: connectionConfig.apiLoginValue,
      password: connectionConfig.apiPasswordValue,
    },
  };

  return axios(requestOptions)
    .then((user) => {
      const result = user.data;
      return result;
    })
    .catch((error) => {
      console.log(error)
      alert(error);
    });
};

export const usersService = {
  getUsers,
  deleteUser,
  createUser,
};
